.resumeList-main {
    align-items: center;
    text-align: center;
}

.resumeList-button:focus {
    outline: none;
}

.resumeList-button {
    border-radius: 50px;
    background-color: black;
    border: none;
    padding: 10px 25px;
    outline: none;
    width: 380px;
}

.resumeList-button-text {
    font-size: 16px;
    text-decoration: none;
    color: #fff;
}

.resumeList-button-img {
    margin-left: 5px;
    height: 16px;
}

.resume-button {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.view-resume {
    min-width: 55%;
}

.resume-viewed {
    width: 100%;
}

.modal-header {
    border-bottom: hidden !important;
}

.resume-list-background {
    background-color: #076e8d;
    box-shadow: inset 0px 2px 5px rgba(0, 0, 0, 0.35);
}

.resume-list {
    display: flex;
    flex-wrap: wrap;
    margin: 5% 5%;
    padding-top: 5%;
    animation-name: newframe;
    animation-duration: 200ms;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: alternate;
    animation-fill-mode: both;
}

@keyframes newframe {
    from {
        height: 0px;
    }
    to {
        height: 1375px;
    }
}