.login-main {
    background-color: white;
    border-radius: 0.5rem;
    padding: 3rem;
    display: flex;
}

.login-main .google-button {
    width: 100%;
    border: 1px solid #b7c2ce;
    padding: 6px 12px;
    background-color: inherit;
    border-radius: 0px;
}

.login-main .google-button:hover {
    color: black;
    border-color: #d3d6da;
    background-color: #d3d6da;
}

.login-main .google-button:focus {
    outline: none;
}

.google-logo {
    height: 30px;
    padding-right: 10px;
}

.login-or-section {
    text-align: center;
    display: flex;
    margin: 2rem;
    margin-top: 1rem;
}

.login-or {
    position: relative;
    top: 12px;
    margin: 0 2px;
}

.strong-line-left, .strong-line-right {
    border-bottom: 1px solid #dfe0e6;
    width: 50%;
}

