.login-form {
    width: 450px;
    height: 450px;
}

.login-margin {
    margin: 5% 0;
}

.welcome-head {
    display: flex;
    justify-content: center;
    font-weight: 400;
    font-size: xx-large;
    margin-bottom: 3%;
}

.login-submit-btn {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 8%;
}

.login-main Button {
    border-radius: 25px;
    padding: 10px 25px;
    background: #FEDE00;
}