@font-face {
    font-family: resume20-name;
    src: url('../../assets/fonts/Oswald/Oswald-Light.ttf')
}

@font-face {
    font-family: resume20-title;
    src: url('../../assets/fonts/Oswald/Oswald-ExtraLight.ttf')
}

.resume20Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume20P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume20Main input, textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume20Main {
    font-family: 'Montserrat';
    font-size: 10px;
    background-color: white;
    width: 766px;
    height: 1096px;
}

.resume20-section-2 {
    display: flex;
    flex-direction: row;
    margin-top: 8%;
}

.resume20-image-section {
    width: 100%;
    text-align: center;
    margin: 15px 0;
}

.resume20-image {
    height: 120px;
}

.resume20-nameBox {
    font-family: resume20-name;
    font-size: 43px;
    font-weight: bold;
    text-align: center;
    line-height: 1.3;
    padding-top: 15px;
    width: 100%;
    color: #5ba49f;
}

.resume20-positionBox {
    font-size: 15px;
    text-align: center;
    letter-spacing: 2px;
    width: 100%;
    margin: 18px 0;
}

.resume20-descriptionBox {
    resize: none;
    overflow: hidden;
}

.resume20-capBox {
    text-transform: uppercase;
}

.resume20-inputs {
    display: flex;
    flex-direction: column;
}

.resume20-section {
    margin-top: 25px;
}

.resume20-left-section {
    width: 300px;
    height: 1096px;
    padding-right: 35px;
    padding-left: 35px;
}

.resume20P2-left-section {
    width: 350px;
    padding-left: 35px;
    padding-right: 35px;
}

.resume20-right-section {
    width: 370px;
    height: 1096px;
    padding-left: 35px;
    padding-right: 35px;
}

.resume20P2-right-section {
    width: 382px;
    padding-left: 35px;
    padding-right: 35px;
}

.resume20-intern-ul textarea {
    font-size: 12px;
}

.resume20-experience-ul textarea {
    font-size: 12px;
}

.resume20-certificate-ul, .resume20-project-ul {
    direction: rtl;
}

.resume20-recommendation-list textarea {
    width: 100%;
    text-align: right;
}

.resume20-section-title {
    font-family: resume20-title;
    letter-spacing: 1.5px;
    font-size: 15px;
    font-weight: 600;
    color: #5ba49f;
}