.resume16Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume16P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume16Main {
    font-family: 'Montserrat';
    font-size: 13px;
    border: 30px solid #fda9d3;
    background-color: #ffffff;
    width: 766px;
    min-height: 1096px;
}

.resume16Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume16Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume16-name-position {
    margin-bottom: 30px;
}

.resume16-capBox {
    text-transform: uppercase;
}

.resume16-nameBox {
    font-size: 60px;
    font-weight: bold;
    line-height: 1;
    color: #81c9e1;
}

.resume16-positionBox {
    font-size: 1.2em;
    font-weight: 600;
    letter-spacing: 1.5px;
    margin-bottom: 2%;
}

.resume16-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume16-section-2 {
    display: flex;
    padding: 45px;
}

.resume16-left-section {
    width: 60%;
    display: flex;
    flex-direction: column;
    padding-right: 35px;
}

.resume16-right-section {
    width: 40%;
    display: flex;
    flex-direction: column;
}

.resume16-inputs {
    display: flex;
    flex-direction: column;
}

.resume16-section {
    margin-top: 25px;
}

.resume16-section-title {
    font-weight: bold;
    font-size: 16px;
    color: #81c9e1;
}

.resume16-text-color {
    color: #81c9e1;
}

.resume16-certificate-ul, .resume16-project-ul {
    direction: ltr;
}

.resume16-recommendation-list textarea {
    width: 100%;
}