.register-main {
    display: flex;
    justify-content: center;
    padding-top: 3%;
    padding-bottom: 10%;
}

.nav {
    justify-content: center;
}

.nav-item {
    width: 20%;
    text-align: center;
}

.nav-link {
    color: black;
    font-weight: 600;
    border: 1px solid transparent;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
}

.nav-link.active {
    color: black;
    background-color: #fff;
    border-color: #dee2e6 #dee2e6 #fff;
}

.nav-link:hover {
    color: black;
    background-color: white;
}

.tab-content {
    box-shadow: 0px 5px 10px 2px rgba(0, 0, 0, 0.19);
}

.register-loader {    
    position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;

    text-align: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 111111;
    color: #fede00;
    background-color: rgba(0, 0, 0, 0.25);
}