@font-face {
    font-family: resume18-name;
    src: url('../../assets/fonts/Lora/Lora-Bold.ttf');
}

.resume18Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume18P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume18Main {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: #ffffff;
    width: 766px;
    height: 1096px;
    border: 15px solid #000000;
}

.resume18Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume18Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume18-resumeHeader {
    height: 190px;
    padding: 6% 5%;
    border-bottom: 10px solid #000000;
}

.resume18-nameBox {
    font-family: resume18-name;
    font-size: 40px;
    text-align: left;
    width: 100%;
    color: #d6ac36;
}

.resume18-positionBox {
    font-size: 15px;
    letter-spacing: 2px;
    margin-top: 1%;
    font-weight: 600;
    width: 100%;
}

.resume18-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume18-section-2 {
    display: flex;
}

.resume18-left-section {
    width: 50%;
    height: 880px;
    border-right: 10px solid #000000;
    padding: 35px;
    display: flex;
    flex-direction: column;
}

.resume18-right-section {
    width: 50%;
    padding: 35px;
    display: flex;
    flex-direction: column;
}

.resume18-inputs {
    display: flex;
    flex-direction: column;
}

.resume18-section {
    margin-top: 25px;
}

.resume18-section-title {
    font-weight: 700;
    font-size: 16px;
    color: #d6ac36;
    letter-spacing: 1.5px;
}

.resume18-text-color {
    color: #d6ac36;
}

.resume18-capBox {
    text-transform: uppercase;
}

.resume18-certificate-ul, .resume18-project-ul {
    direction: ltr;
}

.resume18-recommendation-list textarea {
    width: 100%;
}