@font-face {
    font-family: resume9-name;
    src: url('../../assets/fonts/Anonymous_Pro/AnonymousPro-Regular.ttf');
}

@font-face {
    font-family: resume9-font;
    src: url('../../assets/fonts/Raleway/static/Raleway-Regular.ttf');
}

.resumeNineRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeNineP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeNineMain {
    font-size: 13px;
    border-bottom: 30px solid #ff8882;
    background-color: #f8f8ef;
    width: 796px;
    height: 1096px;
}

.resumeNineMain input {
    font-family: resume9-font;
    letter-spacing: 1.5px;
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeNineMain textarea {
    font-family: resume9-font;
    letter-spacing: 1.5px;
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeNine-resumeHeader {
    height: 270px;
    text-align: center;
    display: flex;
}

.resumeNine-name-position {
    align-self: flex-end;
    text-align: center;
    width: 100%;
}

.resumeNine-capBox {
    text-transform: uppercase;
}

.resumeNine-nameBox {
    font-family: resume9-name;
    font-size: 52px;
    letter-spacing: 1.5px;
    width: 100%;
    height: 70px;
    color: #ff8882;
    text-align: center;
    align-self: center;
}

.resumeNine-positionBox {
    font-size: 1.2em;
    font-weight: 600;
    letter-spacing: 2.8px;
    margin-top: 2%;
    width: 100%;
    text-align: center;
    align-self: center;
}

.resumeNine-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resumeNine-section-2 {
    display: flex;
    margin-top: 5%;
}

.resumeNine-left-section {
            width: 320px;
            padding: 0 45px;
    display: flex;
    flex-direction: column;
}

.resumeNine-right-section {
            width: 390px;
            padding: 0 45px;
    display: flex;
    flex-direction: column;
}

.resumeNine-inputs {
    display: flex;
    flex-direction: column;
}

.resumeNine-section {
    margin-top: 25px;
}

.resumeNine-section-title {
    font-family: resume9-name;
    font-weight: 600;
    letter-spacing: 1.5px;
    font-size: 15px;
    color: #ff8882;
}

.resumeNine-text-color {
    color: #ff8882;
}

.resumeNine-certificate-ul, .resumeNine-project-ul {
    direction: ltr;
}

.resumeNine-recommendation-list textarea {
    width: 100%;
}