.header {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: right;
    position: sticky;
    top: 0;
    z-index: 999;
    padding: 0 5%;
}

.header-logo {
    height: 60px;
}

.header-login {
    position: absolute;
    right: 5%;
}

.nav-heading {
    text-decoration: none;
    font-weight: bold;
}

.nav-heading:hover {
    text-decoration: none;
    font-weight: bold;
}