.resume19Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume19P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume19Main {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: #ffffff;
    width: 766px;
    height: 1096px;
    border: 15px solid #ffffff;
}

.resume19Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume19Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume19-header-design {
    width: 40%;
    height: 40px;
    background-color: #179498;
}

.resume19-resumeHeader {
    height: 136px;
    padding: 20px;
}

.resume19-nameBox {
    height: 55px;
    font-size: 45px;
    text-align: left;
    font-weight: 600;
    width: 100%;
}

.resume19-positionBox {
    font-size: 1.2em;
    letter-spacing: 2px;
    width: 100%;
}

.resume19-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume19-section-2 {
    display: flex;
}

.resume19-left-section {
    width: 40%;
    height: 890px;
    padding: 35px;
    display: flex;
    flex-direction: column;
    background-color: #179498;
    color: #ffffff;
}

.resume19-left-section input {
    color: #ffffff;
}

.resume19-left-section textarea {
    color: #ffffff;
}

.resume19-left-section span {
    color: #ffffff;
}

.resume19-left-section p {
    color: #ffffff;
}

.resume19-right-section {
    width: 60%;
    padding: 35px;
    display: flex;
    flex-direction: column;
}

.resume19-inputs {
    display: flex;
    flex-direction: column;
}

.resume19-section {
    margin-top: 25px;
}

.resume19-section-title {
    font-weight: 700;
    font-size: 15px;
    color: #179498;
    letter-spacing: 1.8px;
}

.resume19-text-color {
    color: #179498;
}

.resume19-capBox {
    text-transform: uppercase;
}

.resume19-certificate-ul, .resume19-project-ul {
    direction: ltr;
}

.resume19-recommendation-list textarea {
    width: 100%;
}